/**
 * 
 * var cache = require('utils/cache.js');
 * import cache from '../cache'
 *  
 *     
 *         string    cache.put('k', 'string');
 *         json      cache.put('k', { "b": "3" }, 2);
 *         array     cache.put('k', [1, 2, 3]);
 *         boolean   cache.put('k', true);
 *     
 *             cache.get('k')
 *         string    cache.get('k', '')
 *         json      cache.get('k', { "a": "1" })
 *     /  
 *         : cache.remove('k');
 *         cache.clear(); 
 * 
 * @type {String}
 */
var postfix = '_mallStore'; //  
/**
 *  
 * @param  {[type]} k []
 * @param  {[type]} v []
 * @param  {[type]} t []
 */
function put(k, v, t) {
    uni.setStorageSync(k, v) 
    var seconds = parseInt(t);
    if (seconds > 0) {
        var timestamp = Date.parse(new Date());
        timestamp = timestamp / 1000 + seconds;
        uni.setStorageSync(k + postfix, timestamp + "")
    } else {
        uni.removeStorageSync(k + postfix)
    }
}


/**
 *  
 * @param  {[type]} k   []
 * @param  {[type]} def []
 */
function get(k, def) {
    var deadtime = parseInt(uni.getStorageSync(k + postfix)) 
    if (deadtime) {
        if (parseInt(deadtime) < Date.parse(new Date()) / 1000) {
            if (def) {
                return def;
            } else {
                return false;
            }
        }
    }
    var res = uni.getStorageSync(k);
    if (res) {
        return res;
    } else {
        if (def == undefined  || def == "") {
            def = false; 
        }
        return def;
    }
}

function remove(k) {
    uni.removeStorageSync(k);
    uni.removeStorageSync(k + postfix);
}

/**
 * 
 * @return {[type]} [description]
 */
function clear() {
    uni.clearStorageSync();
}


module.exports = {
    put: put,
    get: get,
    remove: remove,
    clear: clear,
}